@import './size.scss';
.colorbox{
    width: 20%;
    height: 25%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    cursor: pointer;
    margin-bottom: -3.5px;
}
.singlecolorpalette .colorbox{
    height: 50%;
}
.colorbox.goback{
    background-color: black;
}
.copy-button, .back-button{
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top:50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255,255,255,0.3);
    font-size: 1rem;
    line-height: 30px;
    color: white;
    border:none;
    text-transform: uppercase;
    text-decoration: none;
}

.copy-button{
    opacity: 0;
}
.colorbox:hover .copy-button{
    opacity: 1;
    transition: 0.5s;
}
.box-content{
    position: absolute;
    width: 100%;
    left:0px;
    bottom:0px;
    padding: 10px;
    color:black;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 12px;
}

.light-text{
    color: white;
}
.colorbox .dark-text{
    color: black;
}
.see-more{
    background:rgba(255,255,255,0.3);
    position: absolute;
    border: none;
    right:0px;
    bottom:0px;
    color: white;
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    text-transform: uppercase;
}
.copy-overlay{
    opacity:0;
    z-index: 0;
    width: 100%;
    height: 100%;
    transition: transform 0.6s ease-in-out;
    transform: scale(0.1);
}
.copy-overlay.show{
    opacity: 1;
    transform: scale(50);
    z-index: 10;
    position: absolute;
}
.copy-msg{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 4rem;
    transform: scale(0.1);
    opacity: 0;
    color: white;
}
.copy-msg.show{
    opacity: 1;
    transform: scale(1);
    z-index: 25;
    transition: all 0.4s ease-in-out;
    transition-delay: 0.3s;
}
.copy-msg h1{
    font-weight:400;
    text-shadow: 1px 2px black;
    background: rgba(255,255,255,0.3);
    width: 100%;
    text-align: center ;
    margin-bottom:0;
    padding: 1rem;
    text-transform: uppercase;
}
.copy-msg p{
    font-size:2rem;
    font-weight: 100;
}

@media screen and (max-width:1199.98px) {
    .colorbox{
        width: 25%;
        height: 20%;
    }
    .singlecolorpalette .colorbox{
        width: 20%;
    }
}
@media screen and (max-width:991.98px) {
    .colorbox{
        width: 50%;
        height: 10%;
    }
    .singlecolorpalette .colorbox{
        width: 50%;
        height: 20%;
    }
}
@media screen and (max-width:767.98px) {
    .colorbox{
        width: 100%;
        height: 6%
    }
    .singlecolorpalette .colorbox{
        width: 100%;
        height: 10%;
    }
    .copy-msg h1{
        font-size: 5rem;
    }
}